<template>
  <section>
    <!---->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="filters">
        <el-form-item>
          <el-input :model="filters.project_name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="getProjectList"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleAdd"></el-button>
        </el-form-item>
      </el-form>
    </el-col>
    <!---->
    <el-table :data="projects" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
       <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column v-for="{ prop, label, width } in ShowConfigs" :key="prop" :prop="prop" :label="label" :width="width">
      </el-table-column>
      <el-table-column label="" width="150">
          <template slot-scope="scope">
              <el-dropdown>
                <el-button type="primary">
                      <i class="el-icon-arrow-down el-icon--right"></i>
                </el-button>
    						<el-dropdown-menu slot="dropdown">
    							<el-dropdown-item></el-dropdown-item>
    							<el-dropdown-item></el-dropdown-item>
    							<el-dropdown-item></el-dropdown-item>
    						</el-dropdown-menu>
    					</el-dropdown>
              <el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
              <el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
          </template>
  			</el-table-column>
    </el-table>
    <!---->
    <el-col :span="24" class="toolbar">
      <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
      <el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
      </el-pagination>
    </el-col>
    <!---->
    <el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
      <el-form :model="editForm" label-width="80px" :rules="editFormRules" ref="editForm">
        <el-form-item label="" prop="project_name">
          <el-input v-model="editForm.project_name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="domain">
          <el-input v-model="editForm.domain" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input type="textarea" v-model="editForm.description"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="editFormVisible = false"></el-button>
        <el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
      </div>
    </el-dialog>
    <!---->
    <el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
      <el-form :model="addForm" label-width="80px" :rules="addFormRules" ref="addForm">
        <el-form-item label="" prop="project_name">
          <el-input v-model="editForm.project_name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="domain">
          <el-input v-model="editForm.domain" auto-complete="off"></el-input>
        </el-form-item>
        <el-collapse v-model="activeName" accordion>
          <el-collapse-item title="" name="1">
            <el-form-item label="">
              <el-input type="textarea" v-model="editForm.scan_ports"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input type="textarea" v-model="editForm.scan_ip_range"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="editForm.scan_frequency"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-radio-group v-model="radio2_select_ways">
                <el-radio :label="3">masscan</el-radio>
                <el-radio :label="6">nasscan</el-radio>
              </el-radio-group>
              <el-input v-model="editForm.scan_frequency"></el-input>
            </el-form-item>
          </el-collapse-item>
          <el-collapse-item title="" name="2">
            <div></div>
          </el-collapse-item>
        </el-collapse>
        <el-form-item label="">
          <el-input type="textarea" v-model="editForm.description"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="addFormVisible = false"></el-button>
        <el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
      </div>
    </el-dialog>
  </section>
</template>

<script>
  // import util from '../../common/js/util'
  import {
    getProjectListPage,
    removeProject,
    batchRemoveProject,
    editProject,
    addProject
  } from '@/api/project'

  export default {
    data() {
      this.ShowConfigs = [
        { prop: 'pid', label: 'id', width: 60 },
        { prop: 'create_time', label: '', width: 100 },
        { prop: 'project_name', label: '', width: 100 },
        { prop: 'domain', label: '', width: 80 },
        { prop: 'status', label: '', width: 50 },
        { prop: 'description', label: '', width: 120 }
      ]
      return {
        filters: {
          project_name: ''
        },
        radio2_select_ways: 3,
        activeName: '1',
        total: 0,
        page: 1,
        listLoading: false,
        sels: [], // 
        editFormVisible: false, // 
        editLoading: false,
        projects: [],
        // 
        editForm: {
          pid: 0,
          project_name: '',
          domain: '',
          description: ''
        },
        editFormRules: {
          project_name: [{
            required: true,
            message: '',
            trigger: 'blur'
          }]
        },
        // 
        addForm: {
          pid: 0,
          project_name: '',
          domain: '',
          description: '',
          config: []
        },
        addFormVisible: false, // 
        addLoading: false,
        addFormRules: {
          project_name: [{
            required: true,
            message: '',
            trigger: 'blur'
          }]
        }
      }
    },
    methods: {
      handleCurrentChange(val) {
        this.page = val
        this.getProjectList()
      },
      // 
      getProjectList() {
        const para = {
          page: this.page,
          project_name: this.filters.project_name
        }
        this.listLoading = true
        getProjectListPage(para).then((res) => {
          this.total = res.data.total
          this.projects = res.data.projects
          this.listLoading = false
        })
      },
      // 
      handleDel: function(index, row) {
        this.$confirm('?', '', {
          type: 'warning'
        }).then(() => {
          this.listLoading = true
          const para = {
            pid: row.pid
          }
          removeProject(para).then((res) => {
            this.listLoading = false
            this.$message({
              message: '',
              type: 'success'
            })
            this.getProjectProject()
          })
        }).catch(() => {})
      },
      // 
      handleEdit: function(index, row) {
        this.editFormVisible = true
        this.editForm = Object.assign({}, row)
      },
      // 
      editSubmit: function() {
        this.$refs.editForm.validate((valid) => {
          if (valid) {
            this.$confirm('', '', {}).then(() => {
              this.editLoading = true
              const para = Object.assign({}, this.editForm)
              editProject(para).then((res) => {
                this.editLoading = false
                this.$message({
                  message: '',
                  type: 'success'
                })
                this.$refs['editForm'].resetFields()
                this.editFormVisible = false
                this.getProjectList()
              })
            })
          }
        })
      },
      // 
      handleAdd: function() {
        this.addFormVisible = true
        this.addForm = {
          pid: 0,
          project_name: '',
          domain: '',
          description: '',
          config: []
        }
      },
      // 
      addSubmit: function() {
        this.$refs.addForm.validate((valid) => {
          if (valid) {
            this.$confirm('', '', {}).then(() => {
              this.addLoading = true
              const para = Object.assign({}, this.addForm)
              addProject(para).then((res) => {
                this.addLoading = false
                this.$message({
                  message: '',
                  type: 'success'
                })
                this.$refs['addForm'].resetFields()
                this.addFormVisible = false
                this.getProjectList()
              })
            })
          }
        })
      },
      selsChange: function(sels) {
        this.sels = sels
      },
      // 
      batchRemove: function() {
        var ids = this.sels.map(item => item.id).toString()
        this.$confirm('', '', {
          type: 'warning'
        }).then(() => {
          this.listLoading = true
          const para = {
            ids: ids
          }
          batchRemoveProject(para).then((res) => {
            this.listLoading = false
            this.$message({
              message: '',
              type: 'success'
            })
            this.getProjectList()
          })
        }).catch(() => {})
      }
    },
    mounted() {
      this.getProjectList()
    }
  }
</script>


